import {Link, Paper, TextField} from "@material-ui/core";
import {XGrid} from "@material-ui/x-grid";
import React, {useState} from "react";
import {useQueryClient} from "react-query";
import {SHOW_CUSTOMER_MODAL} from "./context/actions";
import store from "./context/store";
import {fixCustomers} from "./services/utils";
import "./ClientTable.css";

const percentToPixels = (percentage) => {
  return (window.innerWidth * percentage) / 100;
};

const columns = [
  {
    field: "customerName",
    headerName: " ",
    width: percentToPixels(15),
    renderCell: (params) => (
      <Link
        className="name-link"
        onClick={() => store.dispatch(SHOW_CUSTOMER_MODAL(params.row))}
      >
        <b>{params.value}</b>
      </Link>
    ),
  },
  {
    field: "debtor",
    headerName: "",
    width: percentToPixels(6.5),
    cellClassName: "centered",
    renderCell: (params) => {
      if (params.value) {
        return (
          <img src="img/Blackmark.svg" width="19" height="19" alt="blackmark" />
        );
      } else return <div></div>;
    },
  },
  {
    field: "customerGroup",
    headerName: "",
    width: percentToPixels(10),
  },
  {
    field: "firstPerson",
    headerName: " 1",
    width: percentToPixels(10),
  },
  {
    field: "firstPhone",
    headerName: " 1",
    width: percentToPixels(10),
  },
  {
    field: "firstEmail",
    headerName: "Email 1",
    width: percentToPixels(15),
  },
  {
    field: "city",
    headerName: "",
    width: percentToPixels(8),
  },
  {
    field: "info",
    headerName: "",
    width: percentToPixels(20),
  },
];

export default function ClientsTable() {
  const queryClient = useQueryClient();
  const [q, setQ] = useState("");

  const filter = (rows) => {
    return rows.filter(
      (row) =>
        row.customerName?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.customerGroup?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.firstPerson?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.firstPhone?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.firstEmail?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.city?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.info?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1
    );
  };

  return (
    <>
      <TextField
        variant="outlined"
        size="small"
        placeholder=" "
        onChange={(e) => setQ(e.target.value)}
      />
      <Paper elevation={4} className="x-grid">
        <div className="frame">
          <div className="grid-holder">
            <XGrid
              className="customers-grid"
              rows={filter(
                fixCustomers(queryClient.getQueryData("customers") || [])
              )}
              columns={columns}
              sortModel={[{field: "customerName", sort: "asc"}]}
              loading={queryClient.isFetching("customers")}
              rowHeight={35}
              headerHeight={35}
              pagination={true}
              disableSelectionOnClick={true}
            />
          </div>
        </div>
      </Paper>
    </>
  );
}
