import Axios from "axios";

export const fixOrders = (data, id = 0) => {
  return data.map((row) => {
    if (row.orderId === 0) {
      row.id = id;
      row.orderId = id;
    } else {
      row.id = row.orderId;
    }
    !row.date ? (row.date = new Date()) : (row.date = new Date(row.date));
    return row;
  });
};

export const fetchRecentOrders = async () => {
  const response = await Axios.get("/api/orders/");
  const data = response.data;

  if (data) return fixOrders(data);
  return [];
};

export const sortOrders = async (selections, roles) => {
  const form = {
    selectedNo: null,
    selectedCustomer: null,
    selectedEntrepreneur: null,
    selectedOperator: null,
    selectedEmployee: null,
    selectedStatus: null,
    selectedDateFrom: null,
    selectedDateTill: null,
    selectedPayDateFrom: null,
    selectedPayDateTill: null,
    selectedReceiver: null,
    selectedKind: null,
    selectedType: null,
    selectedDetails: "",
  };

  const sendForm = {
    ...form,
    selectedNo: selections?.selectedNo,
    selectedCustomer: selections?.selectedCustomer,
    selectedEntrepreneur: selections?.selectedEntrepreneur,
    selectedOperator: selections?.selectedOperator,
    selectedEmployee: selections?.selectedEmployee,
    selectedStatus: selections?.selectedStatus,
    selectedDateFrom: selections?.selectedDateFrom,
    selectedDateTill: selections?.selectedDateTill,
    selectedPayDateFrom: selections?.selectedPayDateFrom,
    selectedPayDateTill: selections?.selectedPayDateTill,
    selectedReceiver: selections?.selectedReceiver,
    selectedKind: selections?.selectedKind,
    selectedType: selections?.selectedType,
    selectedDetails: selections?.selectedDetails,
  };

  if (roles.includes("ROLE_GROUNDFLOOR")) {
    sendForm.selectedType = {typeId: 7};
  }

  const response = await Axios.post("/api/orders/filter/", sendForm);
  const data = response.data;

  if (data) return fixOrders(data);
  return [];
};
