import {Button} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import RefreshIcon from "@material-ui/icons/Refresh";
import {Field, Form, Formik} from "formik";
import React from "react";
import {useQueryClient} from "react-query";
import {useDispatch, useSelector} from "react-redux";
import * as yup from "yup";
import ComboBox from "./ComboBox";
import {UPDATE_SORTING} from "./context/actions";
import DatePicker from "./DatePicker";
import InputField from "./InputField";
import {EmptySort} from "./models/SortObject";
import "./SortForm.css";
import SwitchControl from "./SwitchControl";

const useStyles = makeStyles(() => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    borderRadius: 4,
    backgroundColor: "white",
    margin: "0 auto",
    width: "52%",
  },
  button: {
    width: "16rem",
    justifySelf: "center",
  },
  clear: {
    backgroundColor: "#fff",
    minWidth: 0,
    justifySelf: "flex-end",
    margin: "0px 0px 1px 0",
    width: "2.4rem",
  },
}));

const validationSchema = yup.object({
  sort: yup.object({
    selectedCustomer: yup.object().nullable(),
    selectedStatus: yup.object().nullable(),
    selectedDateFrom: yup.date().nullable(),
    selectedDateTill: yup.date().nullable(),
    selectedKind: yup.object().nullable(),
    selectedType: yup.object().nullable(),
    selectedDetails: yup.string().trim().max(255).nullable(),
  }),
});

function SortForm() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const queryClient = useQueryClient();
  const authState = useSelector((state) => state.auth);

  const clearAndUnsort = (e, resetForm) => {
    dispatch(UPDATE_SORTING({...EmptySort}, "sort"));
    resetForm(e);
  };

  return (
    <div className={classes.root}>
      <Formik
        validateOnChange={false}
        validateOnMount={false}
        validateOnBlur={false}
        initialValues={{sort: EmptySort, showUserOrders: false}}
        onSubmit={(form) => {
          form.showUserOrders
            ? (form.sort.selectedEmployee = authState.loggedUser)
            : (form.sort.selectedEmployee = null);
          form.sort.selectedDetails = form.sort.selectedDetails.trim();
          dispatch(UPDATE_SORTING(form.sort, "sort"));
        }}
        validationSchema={validationSchema}
      >
        {({values, errors, handleReset, validateField}) => (
          <Form>
            <div className="sort__row__one">
              <Field
                name="sort.selectedCustomer"
                label=""
                error={errors?.customer}
                component={ComboBox}
                validator={validateField}
                getName={(option) => option.customerName || ""}
                options={queryClient.getQueryData("customers") || []}
              />
              <Field
                name="sort.selectedType"
                label=""
                error={errors?.orderType}
                component={ComboBox}
                validator={validateField}
                getName={(option) => option.typeName || ""}
                options={queryClient.getQueryData("orderTypes") || []}
              />
              <Field
                name="sort.selectedKind"
                label=""
                error={errors?.orderKind}
                component={ComboBox}
                validator={validateField}
                getName={(option) => option.kindName || ""}
                options={values.sort.selectedType?.orderKinds || []}
              />
            </div>

            <div className="sort__row__two">
              <div className="datepickers">
                <Field
                  component={DatePicker}
                  label=""
                  name="sort.selectedDateFrom"
                />
                <Field
                  component={DatePicker}
                  label=""
                  name="sort.selectedDateTill"
                />
              </div>
              <Field
                name="sort.selectedStatus"
                label=""
                error={errors?.selectedStatus}
                component={ComboBox}
                validator={validateField}
                getName={(option) => option.name || ""}
                options={queryClient.getQueryData("statuses") || []}
              />
              <Field
                name="sort.selectedDetails"
                label=" "
                error={errors?.selectedDetails}
                numeric={false}
                component={InputField}
                validator={validateField}
              />
            </div>

            <div className="sort__row__three">
              <SwitchControl
                name="showUserOrders"
                isChecked={values.showUserOrders}
                label="  "
              />

              <Button
                color="primary"
                variant="contained"
                className={classes.button}
                type="submit"
              >
                
              </Button>

              <Button
                className={classes.clear}
                onClick={(e) => clearAndUnsort(e, handleReset)}
                variant="contained"
                type="button"
              >
                <RefreshIcon></RefreshIcon>
              </Button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
}

export default SortForm;
