import {TextField} from "@material-ui/core";
import {useFormikContext} from "formik";
import React from "react";
import {calculateM2} from "./services/utils";

const InputField = ({
  label,
  field,
  error,
  readonly = false,
  validator,
  numeric = true,
  phone = false,
  ...props
}) => {
  const {
    form: {setFieldValue},
  } = props;
  const {values: form} = useFormikContext();
  const {onChange, name} = field;
  return (
    <TextField
      {...props}
      {...field}
      error={!!error}
      size="small"
      variant="outlined"
      autoComplete="off"
      label={label}
      InputProps={{readOnly: readonly}}
      onChange={(e) => {
        if (!numeric) onChange(e);
        if (e.target.value.match(/^\d*\.?\d*$/) || e.target.value.length === 0) {
          onChange(e);
        }
        validator(name);
      }}
      onKeyUp={(_) => {
        if (!numeric || phone || !form.order) return;
        calculateM2(form.order);
        const sum =
          form.order.m2 === 0
            ? Math.ceil(form.order.price * form.order.amount + +form.order.fees)
            : Math.ceil(+(form.order.m2 * form.order.price) + +form.order.fees);
        if (sum) setFieldValue("order.finalSum", sum);
      }}
    />
  );
};

export default InputField;
